  position: relative;
}
.timeline-line {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 100%;
  background: linear-gradient(to bottom, #0284c7, #7dd3fc);
  border-radius: 6px;
}
.timeline-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  position: relative;
  z-index: 1;
}
.timeline-item:nth-child(odd) {
  flex-direction: row-reverse;
}
.timeline-point {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: white;
  border: 4px solid #0ea5e9;
  box-shadow: 0 0 0 4px rgba(14, 165, 233, 0.2);
  margin: 0 16px;
  position: relative;
  z-index: 2;
}
.timeline-content {
  width: 45%;
  background: white;
  padding: 16px;
  border-radius: 12px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}
.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 20px -3px rgba(0, 0, 0, 0.15);
}
.phase-pill {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 20px;
  font-size: 9px;
  font-weight: 600;
  margin-bottom: 6px;
}
.quick-facts {
  background: linear-gradient(135deg, #0284c7 0%, #0ea5e9 100%);
  border-radius: 15px;
  padding: 12px;
}
.cta-btn {
  background: linear-gradient(135deg, #0284c7 0%, #0ea5e9 100%);
  transition: transform 0.3s, box-shadow 0.3s;
}
.cta-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px -5px rgba(2, 132, 199, 0.5);
}
.benefit-card {
  transition: all 0.3s ease;
}
.benefit-card:hover {
  transform: translateY(-5px);
}
.pulse-effect {
  animation: pulse 2s infinite;
}
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.reveal .slides section {
